import React from 'react';
import STATS from './enums/stats.js'
import TALENTTYPE from "./enums/talenttype.js";
export default class TalentEffect extends React.Component {
    getFlatDiv(flat) {
        if ((parseInt(this.props.flat) || 0) === 0)
            return ''
        else
            return <div><p>flat: {flat}</p></div>
    }
    render() {
        const cstats = this.props.characterStats
        const effect = this.props.effect
        
        switch(effect.talentType)
        {
            case TALENTTYPE.INHERITEDHP:
            {
                let amount = Math.floor((cstats.getStat(STATS.HP) * (1 + cstats.getStat(STATS.HPPER) / 100) / 100 * parseFloat(this.props.value) + (parseInt(this.props.flat) || 0)) * (1 + (parseFloat(cstats.getStat(STATS.HEAL)) || 0) / 100))
                return  (
                    <div className="TalentEffect">
                        <div><p className="subtitle2">{effect.name}</p></div>
                        <div><p>multiplyer: {this.props.value}%</p></div>
                        {this.getFlatDiv(this.props.flat)}
                        <div><p>amount: {amount}</p></div>
                    </div>
                );
            }
            case TALENTTYPE.BASEATKBUFF:
                {
                    let amount = Math.floor(parseFloat(cstats.getTotalAtk()) / 100 * parseFloat(this.props.value) + (parseInt(this.props.flat) || 0))
                    return  (
                        <div className="TalentEffect">
                            <div><p className="subtitle2">{effect.name}</p></div>
                            <div><p>multiplyer: {this.props.value}%</p></div>
                            {this.getFlatDiv(this.props.flat)}
                            <div><p>amount: {amount}</p></div>
                        </div>
                    );
                }
            case TALENTTYPE.HEAL:
                {
                    let amount;
                    if (effect.type === STATS.ATK)
                        amount = Math.floor(parseFloat(cstats.getTotalAtk()) / 100 * parseFloat(this.props.value) + (parseInt(this.props.flat) || 0)) * (1 + (parseFloat(cstats.getStat(STATS.HEAL)) || 0) / 100)
                    else //(effect.type === STATS.HP) 
                    {
                        amount = Math.floor((cstats.getStat(STATS.HP) * (1 + cstats.getStat(STATS.HPPER) / 100) / 100 * parseFloat(this.props.value) + (parseInt(this.props.flat) || 0)) * (1 + (parseFloat(cstats.getStat(STATS.HEAL)) || 0) / 100))
                    }
                    return  (
                        <div className="TalentEffect">
                            <div><p className="subtitle2">{effect.name}</p></div>
                            <div><p>multiplyer: {this.props.value}%</p></div>
                            {this.getFlatDiv(this.props.flat)}
                            <div><p>amount: {amount}</p></div>
                        </div>
                    );
                }
            case TALENTTYPE.ATTACK:
            default:
                {
                    const damage = Math.floor(parseFloat(cstats.getTotalAtk()) / 100 * parseFloat(this.props.value) + (parseInt(this.props.flat) || 0)) * (1 + (parseFloat(cstats.getStat(this.props.type)) || 0) / 100)
                    const critDamage = Math.floor(damage + damage * (parseFloat(cstats.getStat(STATS.CRITD)) || 0) / 100)
                    const averagedDamage = Math.floor(damage * (1 - (parseFloat(cstats.getStat(STATS.CRITC)) || 0) / 100) + critDamage * (parseFloat(cstats.getStat(STATS.CRITC)) || 0) / 100)
                    return (
                        <div className="TalentEffect">
                            <div><p className="subtitle2">{effect.name}</p></div>
                            <div><p>multiplyer: {this.props.value}%</p></div>
                            {this.getFlatDiv(this.props.flat)}
                            <div><p>damage: {damage}</p></div>
                            <div><p>crit damage: {critDamage}</p></div>
                            <div><p>averaged damage: {averagedDamage}</p></div>
                        </div>
                    );
                }
        }
    }
  }