import React from 'react';
import TalentEffect from './talenteffect.js'
import ScalingInfo from './data/scalinginfo.js'

export default class Talent extends React.Component {
    render() {
      return (
        <div>
          <div><p className="subtitle">{this.props.talent.name}</p></div>
          <div className="PrimarySection">
              {this.props.talent.effects.map((effect) => {
                  return (
                  <div key={effect.name} className="BoxInfo">
                      <TalentEffect effect={effect}
                      value={(effect.value * ScalingInfo[this.props.talent.scaling][this.props.talentLevel]).toFixed(1)}
                      flat={((effect.flat || 0) * ScalingInfo[this.props.talent.scaling][this.props.talentLevel]).toFixed(0)}
                      characterStats={this.props.characterStats}/>
                  </div>)})}
          </div>
        </div>
      );
    }
  }